<template>
  <main>
    <PageHeaderBanner :image-url="data?.image?.url" :title="data?.title" />

    <b-container class="py-3" fluid>
      <b-row>
        <b-col
          cols="12"
          :sm="showSidebar ? '8' : 12"
          :md="showSidebar ? '7' : '10'"
          offset-md="1"
          :xl="showSidebar ? '8' : 10"
        >
          <Adsbygoogle />
          <ArticlePlaceholdersPanel v-if="pending" />
          <ContentRenderer v-else :value="data">
            <ContentRendererMarkdown :value="data.body" />
          </ContentRenderer>
          <Adsbygoogle />
        </b-col>
        <b-col v-if="showSidebar" cols="12" sm="4" md="3" xl="2">
          <RelatedLinksSidebarPanel :busy="pending" class="mb-2 mb-md-3 mb-lg-4" :links="data?.links || []" />
          <ArticleAuthorCard v-if="data?.author" class="mb-3" :author="data?.author" />
        </b-col>
      </b-row>
    </b-container>
  </main>
</template>

<script setup>
  import { computed } from "vue";
  import ArticleAuthorCard from "~/components/cards/ArticleAuthorCard";
  import PageHeaderBanner from "~/components/sections/PageHeaderBanner";
  import ArticlePlaceholdersPanel from "~/components/panels/ArticlePlaceholdersPanel";
  import RelatedLinksSidebarPanel from "~/components/panels/RelatedLinksSidebarPanel";

  definePageMeta({
    layout: "home"
  });

  const route = useRoute();

  const { data, error, pending } = await useLazyAsyncData(
    `content-${route.path}`,
    () => queryContent().where({ _path: route.path }).findOne(),
    { server: true }
  );

  if (error.value) console.log("Error: %o", error.value);

  useContentHead(data);

  const showSidebar = computed(() => pending || data?.value?.links?.length > 0 || data?.value?.author);
</script>
